.works {
  background-color: var(--sec-bcg-color);
  padding-left: 0;
  padding-right: 0;
  padding-top: 72px;
  padding-bottom: 72px;
  @include tablet {
    padding-bottom: 90px;
  }

  &__description {
    padding: 72px 24px 0 24px;
    background-color: var(--sec-bcg-color);
    @include tablet {
      padding: 72px 48px 0 48px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 47px;
    }
    @include desktop {
      padding: 72px 72px 40 72px;
      grid-template-columns: 397px 648px;
      justify-content: space-between;
    }
  }

  &__title {
    margin-bottom: 24px;
    @include inter-font(500, var(--white-text-color));
    font-size: 32px;
    line-height: 120%;
    letter-spacing: -0.64px;
    @include tablet {
      font-size: 54px;
      letter-spacing: -1.08px;
    }
  }
  &__text {
    margin-bottom: 24px;
    @include inter-font(300, var(--white-text-color));
    font-size: 14px;
    line-height: 140%;
    opacity: 0.8;
    @include tablet {
      font-size: 16px;
    }
  }
  &__buttons {
    padding-top: 12px;
    display: flex;
    gap: 24px;
  }
}

.slider {
  background-color: var(--sec-bcg-color);
}
