@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

body {
  height: 100%;
  margin: 0;
}

button {
  border: none;
}

.title {
  text-align: center;
}

a {
  color: currentColor;
  cursor: pointer;
  text-decoration: none;
  transition: all .3s;
}

:active, :hover, :focus {
  outline-offset: 0;
  outline: 0;
}

address {
  font-style: normal;
}

.decoration {
  text-decoration: none;
}

.list {
  list-style: none;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

@font-face {
  font-family: Inter;
  font-weight: 300;
  src: url("inter-light.5aab02e1.ttf") format("truetype");
}

