:root {
  --accent-color: #dc3c3c;
  --bcg-color: #f9f8f4;
  --sec-bcg-color: #1e1e2b;
  --thre-bcg-color: rgba(52, 56, 69, .4);
  --male-bcg: #a5a5bc;
  --female-bcg: #c8b6c7;
  --price-bcg-hover: #f1f1ed;
  --form-bcg-color: #edece9;
  --main-text-color: #1e1e2b;
  --sec-text-color: #292b38;
  --white-text-color: #f9f8f4;
  --red-text-color: #dc3c3c;
  --pure-white: #fff;
  --linguage-text: #989ca9;
  --main-text-color-transparent: rgba(30, 30, 43, .365);
  --link-active-color: rgba(249, 248, 244, .2);
  --black-gradient: to left, rgba(255, 255, 255, .2), rgba(30, 30, 43, .773);
  --icon-gray-color: #8a8990;
  --hover-red-color: #c82828;
  --pressed-red-color: #b41414;
  --hover-social: #86898f;
}

.container {
  width: 100%;
  background-color: var(--sec-bcg-color);
  margin: 0 auto;
  padding: 8px 16px;
}

.logo {
  width: 90px;
  height: 20px;
}

.button {
  max-width: 159px;
  background-color: var(--accent-color);
  cursor: pointer;
  color: var(--white-text-color);
  border-radius: 44px;
  grid-template-columns: 1fr 24px;
  align-items: center;
  padding: 12px 16px;
  font-family: Inter, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: grid;
}

.button:hover, .button:focus {
  background-color: var(--hover-red-color);
}

.button:active {
  background-color: var(--pressed-red-color);
}

.button__icone {
  width: 24px;
  height: 24px;
  fill: currentColor;
}

.button--direction {
  color: var(--main-text-color);
  border: 2px solid var(--accent-color);
  background-color: var(--bcg-color);
  width: 164px;
  gap: 10px;
  padding: 8px 24px;
  font-size: 14px;
  line-height: 120%;
}

.button--direction:hover {
  border: 2px solid var(--hover-red-color);
  background-color: var(--bcg-color);
}

.button--direction:active {
  border: 2px solid var(--pressed-red-color);
  background-color: var(--bcg-color);
}

.button--form {
  margin: 32px auto 0;
}

.button--header {
  border: 2px solid var(--accent-color);
  color: var(--white-text-color);
  background-color: rgba(0, 0, 0, 0);
  grid-template-columns: 1fr;
  padding: 11px 24px;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 120%;
}

.url {
  width: 100%;
  height: 48px;
  background-color: var(--sec-bcg-color);
  justify-content: center;
  align-items: center;
  gap: 2px;
  display: flex;
  position: absolute;
  bottom: -48px;
  left: 0;
}

@media screen and (min-width: 834px) {
  .url {
    display: none;
  }
}

.url__icon {
  width: 6px;
  height: 9px;
  fill: var(--icon-gray-color);
}

.url__text {
  color: var(--pure-white);
  letter-spacing: .36px;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.arrow-batton {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
  position: absolute;
  bottom: 12px;
  right: 12px;
}

.arrow-batton__icon {
  width: 24px;
  height: 24px;
  fill: var(--pure-white);
}

.social-button {
  color: var(--white-text-color);
  border: 2px solid var(--accent-color);
  color: var(--white-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 44px;
  grid-template-columns: 24px auto;
  align-items: center;
  gap: 10px;
  padding: 10px;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 120%;
  transition: all .3s;
  display: grid;
}

@media screen and (min-width: 834px) {
  .social-button {
    padding: 14px 24px;
  }
}

.social-button:hover, .social-button:focus {
  background-color: var(--hover-red-color);
}

.social-button:active {
  background-color: var(--pressed-red-color);
}

.social-button__icon {
  fill: currentColor;
  width: 24px;
  height: 24px;
}

.socials {
  justify-content: center;
  margin-bottom: 32px;
  display: flex;
}

.socials__list {
  gap: 32px;
  display: flex;
}

.socials__icon {
  width: 32px;
  height: 32px;
  fill: var(--white-text-color);
}

.socials__icon:hover, .socials__icon:focus {
  fill: var(--hover-social);
}

.linguage {
  height: 36px;
  background-color: rgba(52, 56, 69, .4);
  border-radius: 24px;
  justify-content: center;
  align-items: center;
